h2{
    padding: 30px 10px 5px 0px;
    margin-left: 15px;
    border-bottom: 3px solid #0A0773;
    font-weight: 500;    
}
p{
    padding:0px 15px 5px 15px;
}

.subtitle{
   border:none;
   padding:5px 0px 0px 0px;     
}

#About .flex-container{
    flex-direction: row;
    flex-wrap: wrap;
   }

   #About #bar h2{
