.card {
    text-align: left;
     box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
     transition: 0.3s;    
     border-radius: 5px;
     background-color: #F2F2F2;
     padding: 5%;
     margin-bottom: 10px;
   }
 
   .card p{   
     padding: 0;  
   }
 
   
   .card:hover {
     box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
   }

   .card ul{
    margin-left: 20px;
  }

  .card img{
    max-width: 300px;
  }